.minhas_fotos{
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;


	.conteudo{
		width: 100%;
		padding: 25px;
		@include desktop{
			width: 73.57%;
		}

		.fotos{
			width: 100%;
			display: block;
			border: 1px solid #dfdfdf;
			padding: 20px 35px;
			border-radius: 5px;

			.central{
				text-align: center;
				width: 100%;
				display: flex;	
				min-height: 350px;
				max-height: 350px;
				align-items: center;
				justify-content: center;
				margin-bottom: 30px;
				img{
					background-color: black;
					height: 380px;
					width: 380px;

				}
			}

			.thumbs{
				width: 100%;
				display: inline-flex;
				flex-wrap: wrap;
				justify-content: flex-start;
				max-height: 350px;
				overflow-y: auto;

				img{
					background-color: #dfdfdf;
					width: 30%;
					margin-right: 5%;
					margin-bottom: 5%;
					cursor: pointer;
					border: 2px solid #fff;
					padding: 2px;
					transition: .3s;

					&:nth-child(3n+3){
						margin-right: 0;
					}

					&:hover{
						border: 2px solid green;
					}
					@include tablet{
						width: 16%;
						&:nth-child(3n+3){
							margin-right: 5%;
						}

						&:nth-child(5n+5){
							margin-right:0;
						}
					}

					@include desktop{
						width: calc(14.28% - 20px);
						margin-right: 20px;
						margin-bottom: 20px;
						&:nth-child(3n+3){
							margin-right: 20px;
						}

						&:nth-child(5n+5){
							margin-right:20px;
						}
						&:nth-child(7n+7){
							margin-right:0px;
						}
					}

					&.active{
						border: 2px solid green;
					}
				}
			}
		}
	}
}
